.mercadoMatch {
    width: auto;
    height: 100%;
    padding: 0;
}

.button {
    /*border: solid var(--color-120) 1px;*/
    border-radius: 3rem;
    border:none;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 12rem;
    width: 64rem;
    padding: 0 0 0 2rem;
    box-shadow: none;
    outline: none;
    /*background-color: var(--color-9);*/
    background-color: transparent;
}

.button:hover {
    background-color: var(--color-4);
    /*background-color: #faedcd;*/
}

.button span {
    white-space: nowrap;
    font-weight: 900;
    font-family: 'Segoe UI', sans-serif;
    font-size: 6rem;
    margin: 0 4rem;
}